import React, { useState } from "react";
import Wrapper from "../../components/Wrapper";
import QRCode from "react-qr-code";
import { SketchPicker } from "react-color";
import styled from "styled-components";

const Account = () => {
  const [color, setColor] = useState("#000");
  const [text, setText] = useState("");

  const handleColor = (color) => {
    setColor(color.hex);
  };
  const handleText = (e) => {
    setText(e.target.value);
  };
  return (
    <>
      <Wrapper>
        <ContainerMain>
          <h1>Generar codigo</h1>
          <div className="container-picker">
            <section>
              <QRCode
                value={text}
                fgColor={color}
                bgColor="#fff"
                level="M"
                title="My code"
              />
            </section>
            <section>
              <SketchPicker color={color} onChange={handleColor} />
            </section>
          </div>
          <input type="text" onChange={handleText} value={text} />
          <button type="button">Guardar qr</button>
        </ContainerMain>
      </Wrapper>
    </>
  );
};

const ContainerMain = styled.main`
  height: calc(100vh - 70px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  .container-picker {
    margin-top: 2rem;
    padding: 1rem;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    section:first-child {
      display: flex;
      align-items: center;
      justify-content: end;
    }
    section:last-child {
      display: flex;
      align-items: center;
      justify-content: start;
    }
  }
  input {
    width: 50rem;
    height: 4rem;
    font-size: 2rem;
  }
  input:focus {
    outline: none;
  }
  button {
    width: 20rem;
    height: 4rem;
    border: none;
    background: #3e50b5;
    color: white;
    cursor: pointer;
  }
`;

export default Account;
