import { authTypes } from "../actions/_auth_actions";

const INITIAL_STATE = {
  user: {
    status: true
  }
};

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case authTypes.LOGIN:
      return {
        ...state,
        user: action.payload
      };
    default:
      return state;
  }
};
