import React from "react";
import { AppBar, Toolbar } from "@material-ui/core";
import styled from "styled-components";
import { useHistory } from "react-router-dom";
import { firebaseAuth } from "../../utils/firebase";

const Navbar = () => {
  const history = useHistory();

  const goTo = (path) => {
    history.push(`/${path}`);
  };
  const logOut = async () => {
    try {
      await firebaseAuth.signOut();
      history.push("/");
    } catch {
      throw new Error("No se pudo cerrar sesi贸n");
    }
  };
  return (
    <AppBar>
      <Toolbar>
        <List>
          <li onClick={() => goTo("home")}>Scannear codigo</li>
          <li onClick={() => goTo("historyCodes")}>Historial de codigos</li>
          <li onClick={() => goTo("generateCode")}>Generar c贸digo</li>
          <li onClick={logOut}>Cerrar sesi贸n</li>
        </List>
      </Toolbar>
    </AppBar>
  );
};

const List = styled.ul`
  display: flex;
  gap: 2rem;
  justify-content: center;
  align-items: center;
  width: 100%;
  li {
    list-style: none;
    cursor: pointer;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
`;

export default Navbar;
