import React, { useState } from "react";
import styled from "styled-components";
import palette from "../../utils/palette";
import { useHistory } from "react-router-dom";
import { firebaseAuth } from "../../utils/firebase";
import { useDispatch } from "react-redux";
import { authLogin } from "../../redux/actions/_auth_actions";

const Login = () => {
  const dispatch = useDispatch();
  const [state, setState] = useState({
    email: "",
    password: "",
    loader: false,
    error: null
  });

  const history = useHistory();

  const goToHome = () => {
    history.push(`/home`);
  };
  const goToRegister = () => {
    history.push("/register");
  };

  const handleInputs = (e) => {
    const { value, name } = e.target;
    setState({
      ...state,
      [name]: value
    });
  };

  const loginInitial = async (e) => {
    e.preventDefault();
    firebaseAuth
      .signInWithEmailAndPassword(state.email, state.password)
      .then(() => {
        firebaseAuth.currentUser.getIdTokenResult().then((idTokenResult) => {
          const { user_id } = idTokenResult.claims;
          const user = {
            ...idTokenResult.claims,
            uid: user_id
          };
          dispatch(authLogin(user));
          setState({
            ...state,
            loader: false
          });
        });
        goToHome();
      })
      .catch((err) => {
        const errCode = err.code;
        if (
          errCode === "auth/invalid-email" ||
          errCode === "auth/wrong-password"
        ) {
          setState({
            ...state,
            loader: false,
            error: "Correo o constrase帽a inv谩lida"
          });
        } else if (errCode === "auth/user-not-found") {
          setState({
            ...state,
            loader: false,
            error: "Usuario inexistente"
          });
        } else {
          setState({
            ...state,
            loader: false,
            error: "Correo o constrase帽a inv谩lida"
          });
        }
      });
  };

  return (
    <ContainerMain>
      <Form onSubmit={loginInitial}>
        <h1>Bienvenido</h1>
        {state.error}
        <div>
          <label htmlFor="">Email</label>
          <input
            required
            type="text"
            name="email"
            value={state.email}
            onChange={handleInputs}
          />
        </div>
        <div>
          <label htmlFor="">Password</label>
          <input
            required
            type="text"
            name="password"
            value={state.password}
            onChange={handleInputs}
          />
        </div>
        <span>
          No tienes cuenta registrate{" "}
          <strong onClick={goToRegister}>aqui</strong>
        </span>
        <button type="submit">Iniciar sesion</button>
      </Form>
    </ContainerMain>
  );
};

const ContainerMain = styled.main`
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

const Form = styled.form`
  height: 50rem;
  width: 50rem;
  border: 1px solid;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  div {
    display: flex;
    flex-direction: column;
    label {
      font-size: 1.6rem;
    }
    input {
      height: 4rem;
      width: 20rem;
    }
  }
  button {
    margin-top: 1rem;
    width: 15rem;
    height: 4rem;
    border: none;
    background: ${palette.blueColor};
    color: ${palette.white};
  }
  span,
  strong {
    font-size: 1.5rem;
  }
  strong {
    cursor: pointer;
  }
`;
export default Login;
