import React, { useState } from "react";
import styled from "styled-components";
import palette from "../../utils/palette";
import { useHistory } from "react-router-dom";
import { firebaseAuth, database } from "../../utils/firebase";
import dayjs from "dayjs";
import { ClipLoader } from "react-spinners";

const Register = () => {
  const [state, setState] = useState({
    email: "",
    password: "",
    name: "",
    loader: false,
    error: null
  });

  const history = useHistory();
  const goToLoggin = () => {
    history.push("/");
  };

  const handleInputs = (e) => {
    const { value, name } = e.target;
    setState({
      ...state,
      [name]: value
    });
  };

  const registerInitial = async (e) => {
    e.preventDefault();
    setState({
      ...state,
      loader: true
    });
    try {
      const response = await firebaseAuth.createUserWithEmailAndPassword(
        state.email,
        state.password
      );

      const createUserData = {
        nameUser: state.name,
        createdUser: dayjs().valueOf(),
        idUser: response.user.uid,
        email: state.email
      };
      await database
        .collection("users")
        .doc(response.user.uid)
        .set(createUserData);
      setState({
        ...state,
        loader: false
      });
      history.push(`/home${response.user.uid}`);
    } catch (err) {
      setState({
        ...state,
        loader: false,
        error: "Ya existe un usuario con este correo"
      });
    }
  };

  return (
    <ContainerMain>
      <Form onSubmit={registerInitial}>
        <h1>Bienvenido</h1>
        {state.error}
        <div>
          <label htmlFor="">Nombre</label>
          <input
            required
            type="text"
            name="name"
            value={state.name}
            onChange={handleInputs}
          />
        </div>
        <div>
          <label htmlFor="">Email</label>
          <input
            required
            type="text"
            name="email"
            value={state.email}
            onChange={handleInputs}
          />
        </div>
        <div>
          <label htmlFor="">Password</label>
          <input
            required
            type="text"
            name="password"
            value={state.password}
            onChange={handleInputs}
          />
        </div>
        <span>
          驴Ya tienes cuenta? Inicia sesi贸n
          <strong onClick={goToLoggin}> aqui</strong>
        </span>
        <button type="submit" disabled={state.loader}>
          {state.loader ? <ClipLoader /> : "Registrarme"}
        </button>
      </Form>
    </ContainerMain>
  );
};

const ContainerMain = styled.main`
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

const Form = styled.form`
  height: 50rem;
  width: 50rem;
  border: 1px solid;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  div {
    display: flex;
    flex-direction: column;
    label {
      font-size: 1.6rem;
    }
    input {
      height: 4rem;
      width: 20rem;
    }
  }
  button {
    margin-top: 1rem;
    width: 15rem;
    height: 4rem;
    border: none;
    background: ${palette.blueColor};
    color: ${palette.white};
  }
  span,
  strong {
    font-size: 1.5rem;
  }
  strong {
    cursor: pointer;
  }
`;
export default Register;
